/* @import url('https://fonts.googleapis.com/css?family=Inconsolata|Lora'); */
@import url('https://fonts.googleapis.com/css?family=Ubuntu|Julee|Lora|Kalam|Merienda');
.App{
  text-align: center;
  background-color: #28282b;
  color: whitesmoke;
  height: 100vh;
  width: 100vw;
  font-family: Ubuntu;
  display: grid;
  grid-template-rows: 18vh 82vh;
}
h1{
  margin: 0px 0px;
  /* padding-top: 5vh; */
}
.switchM{
  cursor: pointer;
  background-color: blanchedalmond;
  border: none;
  font-weight: bold;
}
/* .nav{
  height: 35vh;
} */
.Lower{
  overflow: scroll;
}
a{
  color: whitesmoke;
}
.nav{
  /* width: 20vw; */
  padding-left: 10vw;
  padding-right: 10vw;
  /* border: 1px white solid; */
}
.menu{
  display: flex;
  column-gap: .5cm;
}
.logoText{
  text-align: left;
  padding-top: 5vh;
}
.mode{
  padding-top: 5.5vh;
  width: .8cm;
  height: .8cm;
  cursor: pointer;
}
.logoSection{
  display: flex;
  justify-content: space-between;
  
}
#About{
  text-align: center;
}

.aboutHeadText{
  color: blueviolet;
  font-size: 50px;

}
#about{
  padding-left: 10vw;
  padding-right: 10vw;
  text-align: left;
}
.aboutText{
  /* font-size: Lora; */
  font-family: Lora;
  font-size: 20px;
}
.aboutTextPara{
  margin: 10px 0px;
}
.aboutImg{
  width: 7cm;
  height: 6cm;
  border: none;
  margin-top: 20px;
  border-radius: 10px;

}

#contact{
  padding-left: 10vw;
  padding-right: 10vw;
  padding-top: 5vh;
  text-align: left;
}
.contactImgs{
  padding-left: 5vw;
  padding-right: 5vw;
  display: flex;
  column-gap: .3cm;
}
.contactImg{
  padding-top: 2vh;
  width: 1cm;
}
.contactForm{
	width: 400px;
	height: 350px;
	border: none;
	/* padding-top: 2vh; */
}
#projects{
  padding-left: 10vw;
  padding-right: 10vw;
  padding-top: 5vh;
  text-align: left;
}
.projectsMe{
  padding-left: 5vw;
  padding-right: 5vvw;
}
.projectName{
  color: blueviolet;
  font-size: 35px;
  font-family: Ubuntu;
}

.projectDetails{
  padding-top: 1vh;
  padding-left: 3vw;
}
.projectNameHead{
  display: flex;
  column-gap: .1cm;
  /* justify-content: center; */
  /* align-items: center; */
}
.direct{
  width: .5cm;
  height: .5cm;
  margin-top: 1.5vh;
  
  
}

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
.tagLine{
  margin: 0px 0px;
  font-size: 30px;
  /* width: 35vw; */
  /* white-space: nowrap; */
  /* overflow: hidden; */
  color: blueviolet;
  /* animation: typing 4s steps(40, end); */
  /* font-family: Julee; */
  font-family: Ubuntu;
}

@media screen and (max-width:480px){
	.contactForm{
		width: 350px;
	}
	
}


@media screen and (max-width:415px){
	.contactForm{
		width: 300px;	}
    .projectName{
      font-size: 30px;
    }
}
@media screen and (max-width:385px){
	.contactForm{
		width: 250px;	}
	
}
@media screen and (max-width:330px){
	.contactForm{
		width: 200px;	}
    .aboutImg{
      width: 6cm;
      height: 5cm;
    }

}
@media screen and (min-width:1700px){
	
	.contactForm{
		width: 600px;
		height: 400px;
		
	}
	
}

@media screen and (max-width:300px){
  h1{
    font-size: 30px;
  }
  .aboutHeadText{
    font-size: 30px;
  }
 
  .mode{
    width: .6cm;
    height: .6cm;
  }
  .menuItem{
    font-size: 13px;
  }
  .aboutText{
    font-size: 18px;
  }
  .projectName{
    font-size: 27px;
  }
}
@media screen and (max-width:250px){
  .projectName{
    font-size: 26px;
  }
}


::-webkit-scrollbar {
  width: 10px;
  height: 0px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 

::-webkit-scrollbar-thumb {
  background: blueviolet; 
}


::-webkit-scrollbar-thumb:hover {
  background: blueviolet; 
}
